import React from 'react'

export const ContentCaseItem = ({ image, name, rarity }) => {
  return (
    <div className={`ContentCaseItem CaseCard ${
      rarity === '' ? 'purple' :
      rarity === ' ' ? 'blue' :
      rarity === '' ? 'red' :
      rarity === '' ? 'green' :
      rarity === ' ' ? 'blue' :
      rarity === ' ' ? 'yellow':
      rarity === '' ? 'yellow' :
      rarity === '' ? 'pink' : ''}`}
    >
      <div className={`ContentCaseItemInner ${
          rarity === '' ? 'purple' :
          rarity === ' ' ? 'blue' :
          rarity === '' ? 'red' :
          rarity === '' ? 'green' :
          rarity === ' ' ? 'blue' :
          rarity === ' ' ? 'yellow':
          rarity === '' ? 'yellow' :
          rarity === '' ? 'pink' : ''}`}
      >
        <div className="ContentCaseItemRing"  style={{ border: `2px solid ${
          rarity === '' ? '#9830b3' :
          rarity === ' ' ? '#3092bb' :
          rarity === '' ? '#db4343' :
          rarity === '' ? '#1dd87e' :
          rarity === ' ' ? '#3092bb' :
          rarity === ' ' ? '#cbde22':
          rarity === '' ? '#cbde22' :
          rarity === '' ? '#c9405d' : ''}`}}
        >
            <img src={image} alt={name} />
        </div>
        <div className='ContentCaseItemDescription'>
            <p className='name'>{name}</p>
            <span className='categoryName' style={{ color: `${
              rarity === '' ? '#7257a8' :
              rarity === ' ' ? '#33687e' :
              rarity === '' ? '#7f3535' :
              rarity === '' ? '#307d58' :
              rarity === ' ' ? '#33687e' :
              rarity === ' ' ? '#7c842d':
              rarity === '' ? '#7c842d' :
              rarity === '' ? '#7f357b' : ''}`}}
            >{rarity}</span>
        </div>
      </div>
    </div>
  )
}
