import React from 'react'

import './ContentsCase.scss';

export const ContentsCase = ({ items }) => {
  return (
    <div className='ContentsCase'>
      <h2> </h2>
      <div className='ProfileBottom ContentWrapper'>
        {items?.map((item, index) => (
          <div className={`ProfileBottomCard ${
            item.rarity === '' ? 'purple' :
            item.rarity === ' ' ? 'blue' :
            item.rarity === '' ? 'red' :
            item.rarity === '' ? 'green' :
            item.rarity === ' ' ? 'blue' :
            item.rarity === ' ' ? 'yellow': 
            item.rarity === '' ? 'yellow' :
            item.rarity === '' ? 'pink' : ''}`} key={index}
          >
            <div className={`ProfileCardInner ${
              item.rarity === '' ? 'purple' :
              item.rarity === ' ' ? 'blue' :
              item.rarity === '' ? 'red' :
              item.rarity === '' ? 'green' :
              item.rarity === ' ' ? 'blue' :
              item.rarity === ' ' ? 'yellow': 
              item.rarity === '' ? 'yellow' :
              item.rarity === '' ? 'pink' : ''}`}
            >
              <div className="ProfileBottomBlockRing" style={{ border: `2px solid ${
                item.rarity === '' ? '#9830b3' :
                item.rarity === ' ' ? '#3092bb' :
                item.rarity === '' ? '#db4343' :
                item.rarity === '' ? '#1dd87e' :
                item.rarity === ' ' ? '#3092bb' :
                item.rarity === ' ' ? '#cbde22':
                item.rarity === '' ? '#cbde22' : 
                item.rarity === '' ? '#c9405d' : ''}`}}
              >
                <img src={item.image} alt="" />
              </div>
              <div className='ProfileBottomBlockDescription'>
                <p className='name'>{item.name}</p>
                <span className='categoryName' style={{ color: `${
                  item.rarity === '' ? '#7257a8' :
                  item.rarity === ' ' ? '#33687e' :
                  item.rarity === '' ? '#7f3535' :
                  item.rarity === '' ? '#307d58' :
                  item.rarity === ' ' ? '#33687e' :
                  item.rarity === ' ' ? '#7c842d':
                  item.rarity === '' ? '#7c842d' :
                  item.rarity === '' ? '#7f357b' : ''}`}}
                >{item.rarity}</span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
